nav{
    background: rgba(0, 0, 0, .3);
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translate3d(-50%,0,0);
    bottom: 2rem;
    display: flex;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}

nav a {
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
}

nav a:hover {
    background: rgba(0, 0, 0, .3);

}

nav a.active {
    background: var(--color-bg);
    color: var(--color-pastel-blue-2dark);
}

.navAnimation{
    animation-name: nav-load;
    animation-duration: 600ms;
    animation-delay: 600ms;
    animation-timing-function: ease-in;
}

@keyframes nav-load{
    0%{
        transform: translate3d(-50%,100px,0);
        opacity: 0;
    }

    100%{
        transform: translateY(-50%,0px,0);
        opacity: 1;
    }
}

