* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --color-bg: #EFEDE9;
    --color-bg-variant: #A1CE6D;
    --color-primary: #DAAD30;
    --color-primary-variant: rgba(250,179,162, 0.4);
    --color-black: #000;
    --color-white:#fff;
    --color-second:#94B39A;
    --color-second-dark:#D7E8FC;
    --color-light: rgba(0,0,0, 0.7);
    --color-dark:#3C4F5B;
    --color-green:#3E550D;
    --color-orange:#FF7B00;
    --color-dark-red:#870B30;
    --color-pastel-blue:#9FBEE0;
    --color-pastel-blue-dark:#2E8BC2;
    --color-pastel-blue-2dark:#243274;
    --color-pastel-purple-light:#D3AFDB;
    --color-pastel-purple:#A24C90;
    --color-pastel-purple-dark:#61408D;
    --color-pastel-ligth-green:#9DD7C9;
    --color-pastel-2ligth-green:#0393B9;
    --color-pastel-ligth-yellow:#F6EEB6;
    --color-pastel-black-variant:rgba(0,0,0, 0.2);



    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;

    --transition: all 400ms ease;
    --transition-in: all 400ms ease-in;
    --transition-out: all 400ms ease-out;
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar{
    display: none;
}

body {
    font-family: "Poppins", sans-serif;
    background: var(--color-bg);
    color: var(--color-white);
    line-height: 1.7;
    
}

/* General Styles */

.container {
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1, h2, h3, h4, h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}


section {
    margin-top: 8rem;
}

section > h2, section > h5 {
    text-align: center;
    color: var(--color-black);
}

section > h2 {
    color: var(--color-black);
    margin-bottom: 3rem;
}

.text-light {
    color: var(--color-light);
}

a {
    color: var(--color-primary);
    transition: var(--transition);
}

a:hover {
    color: var(--color-second);
}

.btn {
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover {
    background-color: var(--color-second);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background-color: var(--color-primary);
    color: var(--color-bg);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

/* media queries - << Medium >> */
@media screen and (max-width: 1024px) {
    .container {
        width: var(--container-width-md);
    }

    section {
        margin-top: 6rem;
    }
}


/* media queries - << Small >> */
@media screen and (max-width: 600px) {
    .container {
        width: var(--container-width-sm);
    }

    section > h2 {
        margin-top: 2rem;
    }
}